ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.page {
  letter-spacing: .5px;
  scroll-behavior: smooth;
  font-family: Red Hat Display, sans-serif;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  transition: all .5s;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  transform: translateX(0);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  z-index: 3;
  transform: translateX(0);
}

.page__body {
  min-width: 380px;
}

.go__back {
  color: #fff;
  background-color: #0dd0ba;
  border-radius: 100px;
  padding: 25px;
  font-weight: 700;
  transition: transform .5s;
  position: fixed;
  bottom: 25px;
  right: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .35);
}

.go__back:hover {
  transform: scale(1.1);
}

.darkalljs {
  transition: all .5s;
}

.darkalljs.dark {
  background-color: #0e1219;
}

.whitealljs.white {
