}

.menu__info {
  height: 100vh;
  top: 295px;
}

.menu__contents {
  flex-direction: column;
  gap: 12px;
  margin-top: 5px;
  display: flex;
}

.menu__title {
  color: rgba(0, 0, 0, 0);
  text-align: center;
  background: linear-gradient(90deg, #0dd0ba 0%, #2a86e5 100%);
  -webkit-background-clip: text;
  text-align: center;
  -webkit-background-clip: text;
  background-clip: text;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  display: flex;
}

@media (min-width: 768px) {
  .menu__title {
    display: none;
  }
}

.menu__title:after {
  content: "";
  width: 25px;
  height: 5px;
  background: linear-gradient(90deg, #0dd0ba 0%, #2a86e5 100%);
  border-radius: 40px;
  display: block;
}

.menu__lists {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  display: flex;
}

.menu__itemss {
  background: linear-gradient(90deg, #0dd0ba 0%, #2a86e5 100%);
