  color: rgba(0, 0, 0, 0);
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
}

.menu__itemss--none {
  display: none;
}

@media (min-width: 768px) {
  .menu__itemss--none {
    text-align: -webkit-center;
    display: block;
  }

  .menu__itemss--none:after {
    content: "";
    width: 25px;
    height: 5px;
    background: linear-gradient(90deg, #0dd0ba 0%, #2a86e5 100%);
