  content: "";
  width: 100%;
  height: 1px;
  transform-origin: 0;
  background-color: #0dd0ba;
  transition: transform .5s;
  display: block;
  position: absolute;
  bottom: 1px;
  transform: scale(0);
}

.menu__link:hover {
  color: #0dd0ba;
  transform: scale(1.1);
}

